import React, { useState } from 'react';
import { Search, Filter, Download, RotateCcw } from 'lucide-react';

interface LogFiltersProps {
  onFiltersChange: (filters: any) => void;
  onExport: (format: 'csv' | 'json') => void;
  filters: any;
  logType: 'audit' | 'system' | 'security' | 'performance';
}

const LogFilters: React.FC<LogFiltersProps> = ({ 
  onFiltersChange, 
  onExport, 
  filters, 
  logType 
}) => {
  const [isExpanded, setIsExpanded] = useState(false);
  const [localFilters, setLocalFilters] = useState(filters);

  const handleFilterChange = (key: string, value: string | boolean | number | undefined) => {
    const newFilters = { ...localFilters, [key]: value };
    setLocalFilters(newFilters);
    onFiltersChange(newFilters);
  };

  const resetFilters = () => {
    const emptyFilters = {
      page: 1,
      pageSize: 20,
      search: '',
      startDate: '',
      endDate: '',
      sortBy: 'timestamp',
      sortDirection: 'desc' as const
    };
    setLocalFilters(emptyFilters);
    onFiltersChange(emptyFilters);
  };

  const getSeverityOptions = () => {
    if (logType === 'audit' || logType === 'security') {
      return ['Information', 'Warning', 'Error', 'Critical'];
    }
    return [];
  };

  const getLevelOptions = () => {
    if (logType === 'system') {
      return ['Debug', 'Information', 'Warning', 'Error', 'Critical'];
    }
    return [];
  };

  const getPerformanceLevelOptions = () => {
    if (logType === 'performance') {
      return ['Excellent', 'Good', 'Normal', 'Slow', 'Critical'];
    }
    return [];
  };

  return (
    <div className="bg-white dark:bg-gray-800 p-4 rounded-lg shadow-sm border border-gray-200 dark:border-gray-700 mb-6">
      <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
        {/* Search */}
        <div className="flex-1 max-w-md">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 h-4 w-4 text-gray-400" />
            <input
              type="text"
              placeholder="Search logs..."
              className="w-full pl-10 pr-4 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                         bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                         focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              value={localFilters.search || ''}
              onChange={(e) => handleFilterChange('search', e.target.value)}
            />
          </div>
        </div>

        {/* Actions */}
        <div className="flex items-center gap-2">
          <button
            onClick={() => setIsExpanded(!isExpanded)}
            className="flex items-center gap-2 px-3 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 
                       bg-gray-100 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 
                       rounded-md hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors"
          >
            <Filter className="h-4 w-4" />
            {isExpanded ? 'Hide Filters' : 'Show Filters'}
          </button>
          
          <button
            onClick={resetFilters}
            className="flex items-center gap-2 px-3 py-2 text-sm font-medium text-gray-700 dark:text-gray-300 
                       bg-gray-100 dark:bg-gray-700 border border-gray-300 dark:border-gray-600 
                       rounded-md hover:bg-gray-200 dark:hover:bg-gray-600 transition-colors"
          >
            <RotateCcw className="h-4 w-4" />
            Reset
          </button>

          <div className="relative">
            <button
              onClick={() => onExport('csv')}
              className="flex items-center gap-2 px-3 py-2 text-sm font-medium text-white 
                         bg-primary-600 border border-transparent rounded-md hover:bg-primary-700 
                         transition-colors"
            >
              <Download className="h-4 w-4" />
              Export CSV
            </button>
          </div>
        </div>
      </div>

      {/* Expanded Filters */}
      {isExpanded && (
        <div className="mt-4 pt-4 border-t border-gray-200 dark:border-gray-700">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
            {/* Date Range */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Start Date
              </label>
              <input
                type="date"
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                           bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                           focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                value={localFilters.startDate || ''}
                onChange={(e) => handleFilterChange('startDate', e.target.value)}
              />
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                End Date
              </label>
              <input
                type="date"
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                           bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                           focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                value={localFilters.endDate || ''}
                onChange={(e) => handleFilterChange('endDate', e.target.value)}
              />
            </div>

            {/* Log Type Specific Filters */}
            {(logType === 'audit' || logType === 'security') && (
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Severity
                </label>
                <select
                  className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                             bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                             focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                  value={localFilters.severity || ''}
                  onChange={(e) => handleFilterChange('severity', e.target.value)}
                >
                  <option value="">All Severities</option>
                  {getSeverityOptions().map(severity => (
                    <option key={severity} value={severity}>{severity}</option>
                  ))}
                </select>
              </div>
            )}

            {logType === 'system' && (
              <>
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                    Level
                  </label>
                  <select
                    className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                               bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                               focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                    value={localFilters.level || ''}
                    onChange={(e) => handleFilterChange('level', e.target.value)}
                  >
                    <option value="">All Levels</option>
                    {getLevelOptions().map(level => (
                      <option key={level} value={level}>{level}</option>
                    ))}
                  </select>
                </div>
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                    Status
                  </label>
                  <select
                    className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                               bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                               focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                    value={localFilters.isResolved === undefined ? '' : localFilters.isResolved.toString()}
                    onChange={(e) => handleFilterChange('isResolved', e.target.value === '' ? undefined : e.target.value === 'true')}
                  >
                    <option value="">All</option>
                    <option value="false">Unresolved</option>
                    <option value="true">Resolved</option>
                  </select>
                </div>
              </>
            )}

            {logType === 'performance' && (
              <div>
                <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                  Performance Level
                </label>
                <select
                  className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                             bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                             focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                  value={localFilters.performanceLevel || ''}
                  onChange={(e) => handleFilterChange('performanceLevel', e.target.value)}
                >
                  <option value="">All Levels</option>
                  {getPerformanceLevelOptions().map(level => (
                    <option key={level} value={level}>{level}</option>
                  ))}
                </select>
              </div>
            )}

            {/* Sort Options */}
            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Sort By
              </label>
              <select
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                           bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                           focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                value={localFilters.sortBy || 'timestamp'}
                onChange={(e) => handleFilterChange('sortBy', e.target.value)}
              >
                <option value="timestamp">Timestamp</option>
                {logType === 'audit' && <option value="action">Action</option>}
                {logType === 'system' && <option value="level">Level</option>}
                {logType === 'security' && <option value="eventType">Event Type</option>}
                {logType === 'performance' && <option value="duration">Duration</option>}
              </select>
            </div>

            <div>
              <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-1">
                Sort Direction
              </label>
              <select
                className="w-full px-3 py-2 border border-gray-300 dark:border-gray-600 rounded-md 
                           bg-white dark:bg-gray-700 text-gray-900 dark:text-white
                           focus:ring-2 focus:ring-primary-500 focus:border-transparent"
                value={localFilters.sortDirection || 'desc'}
                onChange={(e) => handleFilterChange('sortDirection', e.target.value)}
              >
                <option value="desc">Newest First</option>
                <option value="asc">Oldest First</option>
              </select>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default LogFilters;