import React, { useState, useEffect } from 'react';
import { Eye, BarChart3, Clock, TrendingUp, AlertTriangle } from 'lucide-react';
import { logService } from '../../services/logService';
import { PerformanceLog, PerformanceLogFilters } from '../../types';
import LogFilters from '../../components/logs/LogFilters';
import toast from 'react-hot-toast';

const PerformanceLogs: React.FC = () => {
  const [logs, setLogs] = useState<PerformanceLog[]>([]);
  const [loading, setLoading] = useState(true);
  const [totalCount, setTotalCount] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [pageSize] = useState(20);
  const [selectedLog, setSelectedLog] = useState<PerformanceLog | null>(null);
  const [showModal, setShowModal] = useState(false);
  const [filters, setFilters] = useState<PerformanceLogFilters>({
    page: 1,
    pageSize: 20,
    search: '',
    sortBy: 'timestamp',
    sortDirection: 'desc'
  });

  const fetchLogs = async () => {
    try {
      setLoading(true);
      const response = await logService.getPerformanceLogs(filters);
      setLogs(response.data);
      setTotalCount(response.totalCount);
    } catch (error) {
      console.error('Error fetching performance logs:', error);
      toast.error('Failed to fetch performance logs');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchLogs();
  }, [filters]);

  const handleFiltersChange = (newFilters: PerformanceLogFilters) => {
    setFilters({ ...newFilters, page: 1 });
    setCurrentPage(1);
  };

  const handleExport = async (format: 'csv' | 'json') => {
    try {
      await logService.exportLogs('performance', filters, format);
      toast.success(`Performance logs exported as ${format.toUpperCase()}`);
    } catch (error) {
      console.error('Error exporting logs:', error);
      toast.error('Failed to export logs');
    }
  };

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    setFilters({ ...filters, page });
  };

  const getPerformanceLevelColor = (level: string) => {
    switch (level.toLowerCase()) {
      case 'excellent': return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200';
      case 'good': return 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200';
      case 'normal': return 'bg-gray-100 text-gray-700 dark:bg-gray-700 dark:text-gray-300';
      case 'slow': return 'bg-yellow-100 text-yellow-700 dark:bg-yellow-900 dark:text-yellow-300';
      case 'critical': return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200';
      default: return 'bg-gray-100 text-gray-700 dark:bg-gray-700 dark:text-gray-300';
    }
  };

  const getStatusCodeColor = (statusCode: number) => {
    if (statusCode >= 200 && statusCode < 300) return 'text-green-600 dark:text-green-400';
    if (statusCode >= 300 && statusCode < 400) return 'text-blue-600 dark:text-blue-400';
    if (statusCode >= 400 && statusCode < 500) return 'text-yellow-600 dark:text-yellow-400';
    if (statusCode >= 500) return 'text-red-600 dark:text-red-400';
    return 'text-gray-600 dark:text-gray-400';
  };

  const formatDuration = (duration: string) => {
    try {
      const ms = parseFloat(duration.replace(/[^\d.]/g, ''));
      if (ms < 1000) return `${ms.toFixed(2)}ms`;
      return `${(ms / 1000).toFixed(2)}s`;
    } catch {
      return duration;
    }
  };

  const formatBytes = (bytes: number | undefined) => {
    if (!bytes) return 'N/A';
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    if (bytes === 0) return '0 Bytes';
    const i = Math.floor(Math.log(bytes) / Math.log(1024));
    return Math.round(bytes / Math.pow(1024, i) * 100) / 100 + ' ' + sizes[i];
  };

  const formatTimestamp = (timestamp: string) => {
    return new Date(timestamp).toLocaleString();
  };

  const viewLogDetails = async (logId: number) => {
    try {
      const log = await logService.getPerformanceLogById(logId);
      setSelectedLog(log);
      setShowModal(true);
    } catch (error) {
      console.error('Error fetching log details:', error);
      toast.error('Failed to fetch log details');
    }
  };

  const totalPages = Math.ceil(totalCount / pageSize);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900 dark:text-white">Performance Logs</h1>
          <p className="text-gray-600 dark:text-gray-400">
            Monitor application performance and resource usage
          </p>
        </div>
        <div className="text-sm text-gray-500 dark:text-gray-400">
          {totalCount} total logs
        </div>
      </div>

      {/* Filters */}
      <LogFilters
        filters={filters}
        onFiltersChange={handleFiltersChange}
        onExport={handleExport}
        logType="performance"
      />

      {/* Logs Table */}
      <div className="bg-white dark:bg-gray-800 shadow-sm rounded-lg border border-gray-200 dark:border-gray-700">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200 dark:divide-gray-700">
            <thead className="bg-gray-50 dark:bg-gray-700">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Operation
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Duration
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Performance
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Memory
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Timestamp
                </th>
                <th className="px-6 py-3 text-right text-xs font-medium text-gray-500 dark:text-gray-300 uppercase tracking-wider">
                  Actions
                </th>
              </tr>
            </thead>
            <tbody className="bg-white dark:bg-gray-800 divide-y divide-gray-200 dark:divide-gray-700">
              {loading ? (
                <tr>
                  <td colSpan={7} className="px-6 py-12 text-center">
                    <div className="flex items-center justify-center">
                      <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary-600"></div>
                      <span className="ml-2 text-gray-500 dark:text-gray-400">Loading performance logs...</span>
                    </div>
                  </td>
                </tr>
              ) : logs.length === 0 ? (
                <tr>
                  <td colSpan={7} className="px-6 py-12 text-center text-gray-500 dark:text-gray-400">
                    No performance logs found
                  </td>
                </tr>
              ) : (
                logs.map((log) => (
                  <tr key={log.id} className="hover:bg-gray-50 dark:hover:bg-gray-700">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <BarChart3 className="h-4 w-4 text-gray-400 mr-2" />
                        <div>
                          <div className="text-sm font-medium text-gray-900 dark:text-white">
                            {log.operation}
                          </div>
                          <div className="text-sm text-gray-500 dark:text-gray-400">
                            {log.httpMethod} {log.requestPath}
                          </div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <Clock className="h-4 w-4 text-gray-400 mr-2" />
                        <div>
                          <div className="text-sm font-medium text-gray-900 dark:text-white">
                            {formatDuration(log.duration)}
                          </div>
                          {log.isSlowQuery && (
                            <div className="flex items-center text-yellow-600 dark:text-yellow-400">
                              <TrendingUp className="h-3 w-3 mr-1" />
                              <span className="text-xs">Slow Query</span>
                            </div>
                          )}
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`text-sm font-medium ${getStatusCodeColor(log.statusCode)}`}>
                        {log.statusCode}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getPerformanceLevelColor(log.performanceLevel)}`}>
                        {log.performanceLevel}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900 dark:text-white">
                        {formatBytes(log.memoryUsedMB ? log.memoryUsedMB * 1024 * 1024 : undefined)}
                      </div>
                      {log.cpuUsagePercent && (
                        <div className="text-sm text-gray-500 dark:text-gray-400">
                          CPU: {log.cpuUsagePercent}%
                        </div>
                      )}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-900 dark:text-white">
                      {formatTimestamp(log.timestamp)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <div className="flex items-center justify-end space-x-2">
                        <button
                          onClick={() => viewLogDetails(log.id)}
                          className="text-primary-600 hover:text-primary-900 dark:hover:text-primary-400"
                        >
                          <Eye className="h-4 w-4" />
                        </button>
                        {log.alertTrigger && (
                          <AlertTriangle className="h-4 w-4 text-red-500" />
                        )}
                      </div>
                    </td>
                  </tr>
                ))
              )}
            </tbody>
          </table>
        </div>

        {/* Pagination */}
        {totalPages > 1 && (
          <div className="bg-white dark:bg-gray-800 px-4 py-3 border-t border-gray-200 dark:border-gray-700">
            <div className="flex items-center justify-between">
              <div className="text-sm text-gray-700 dark:text-gray-300">
                Showing {((currentPage - 1) * pageSize) + 1} to {Math.min(currentPage * pageSize, totalCount)} of {totalCount} results
              </div>
              <div className="flex items-center space-x-2">
                <button
                  onClick={() => handlePageChange(currentPage - 1)}
                  disabled={currentPage === 1}
                  className="px-3 py-1 text-sm font-medium text-gray-500 dark:text-gray-400 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-md hover:bg-gray-50 dark:hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Previous
                </button>
                <span className="text-sm text-gray-700 dark:text-gray-300">
                  Page {currentPage} of {totalPages}
                </span>
                <button
                  onClick={() => handlePageChange(currentPage + 1)}
                  disabled={currentPage === totalPages}
                  className="px-3 py-1 text-sm font-medium text-gray-500 dark:text-gray-400 bg-white dark:bg-gray-800 border border-gray-300 dark:border-gray-600 rounded-md hover:bg-gray-50 dark:hover:bg-gray-700 disabled:opacity-50 disabled:cursor-not-allowed"
                >
                  Next
                </button>
              </div>
            </div>
          </div>
        )}
      </div>

      {/* Log Details Modal */}
      {showModal && selectedLog && (
        <div className="fixed inset-0 z-50 overflow-y-auto">
          <div className="flex items-end justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 transition-opacity" aria-hidden="true">
              <div className="absolute inset-0 bg-gray-500 opacity-75" onClick={() => setShowModal(false)}></div>
            </div>

            <div className="inline-block align-bottom bg-white dark:bg-gray-800 rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-4xl sm:w-full">
              <div className="bg-white dark:bg-gray-800 px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <div className="flex items-center justify-between mb-4">
                  <h3 className="text-lg font-medium text-gray-900 dark:text-white">
                    Performance Log Details
                  </h3>
                  <button
                    onClick={() => setShowModal(false)}
                    className="text-gray-400 hover:text-gray-600 dark:hover:text-gray-200"
                  >
                    
                  </button>
                </div>

                <div className="space-y-4">
                  <div className="grid grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Operation</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">{selectedLog.operation}</p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Performance Level</label>
                      <span className={`inline-flex px-2 py-1 text-xs font-semibold rounded-full ${getPerformanceLevelColor(selectedLog.performanceLevel)}`}>
                        {selectedLog.performanceLevel}
                      </span>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Duration</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">{formatDuration(selectedLog.duration)}</p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Status Code</label>
                      <span className={`text-sm font-medium ${getStatusCodeColor(selectedLog.statusCode)}`}>
                        {selectedLog.statusCode}
                      </span>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Memory Used</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {formatBytes(selectedLog.memoryUsedMB ? selectedLog.memoryUsedMB * 1024 * 1024 : undefined)}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">CPU Usage</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {selectedLog.cpuUsagePercent ? `${selectedLog.cpuUsagePercent}%` : 'N/A'}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Request Size</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {formatBytes(selectedLog.requestSizeBytes)}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Response Size</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {formatBytes(selectedLog.responseSizeBytes)}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Database Time</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {selectedLog.databaseTime ? formatDuration(selectedLog.databaseTime) : 'N/A'}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Query Count</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {selectedLog.databaseQueryCount || 'N/A'}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Cache Hits</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {selectedLog.cacheHitCount || 'N/A'}
                      </p>
                    </div>
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Cache Misses</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {selectedLog.cacheMissCount || 'N/A'}
                      </p>
                    </div>
                  </div>

                  {selectedLog.requestPath && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Request Path</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white font-mono">
                        {selectedLog.httpMethod} {selectedLog.requestPath}
                      </p>
                    </div>
                  )}

                  {selectedLog.controller && selectedLog.action && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Controller/Action</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {selectedLog.controller}.{selectedLog.action}
                      </p>
                    </div>
                  )}

                  {selectedLog.externalServiceTime && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">External Service Time</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white">
                        {formatDuration(selectedLog.externalServiceTime)}
                      </p>
                    </div>
                  )}

                  {selectedLog.correlationId && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Correlation ID</label>
                      <p className="mt-1 text-sm text-gray-900 dark:text-white font-mono">{selectedLog.correlationId}</p>
                    </div>
                  )}

                  {selectedLog.alertTrigger && (
                    <div className="bg-red-50 dark:bg-red-900 border border-red-200 dark:border-red-700 p-3 rounded">
                      <div className="flex items-center">
                        <AlertTriangle className="h-5 w-5 text-red-500 mr-2" />
                        <div>
                          <span className="text-sm font-medium text-red-800 dark:text-red-200">
                            Performance Alert
                          </span>
                          <p className="text-sm text-red-700 dark:text-red-300 mt-1">
                            {selectedLog.alertTrigger}
                          </p>
                        </div>
                      </div>
                    </div>
                  )}

                  {selectedLog.metadata && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300">Metadata</label>
                      <pre className="mt-1 text-sm text-gray-900 dark:text-white bg-gray-50 dark:bg-gray-700 p-2 rounded overflow-x-auto">
                        {selectedLog.metadata}
                      </pre>
                    </div>
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default PerformanceLogs;